
.nav .open>a {
  background:transparent;
}

.navbar-nav .nav-link{
  text-transform: capitalize;
  font-size: 18px;
  letter-spacing: .5px;
  color: $light;
  padding: 10px 15px!important;
}

.nav-social{
  a{
    width: 40px;
    height: 40px;
    text-align: center;
    color: $light;
    display:inline-block;
    padding-top: 8px;
    background:rgba(255,255,255,.2);
    border-radius: 100%;
  }
}

.navbar-brand .logo{
  color: $light;
}


.navigation {
    transition: .3s ease;
    border-bottom:1px solid transparent;
  }

.fixed-nav{
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  background: $secondary-color!important;
  border-bottom:1px solid rgba(255,255,255,.03);
}
