.footer-socials{
  li a{
    color: $light;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-size: 22px;
    margin-right:10px;

    &:hover{
      color: $primary-color;
    }
  }
}

.footer{
  padding: 80px 0px 60px;
  p{
    color: rgba(255,255,255,.6);
  }
}

.backtop{
  width: 40px;
  height: 40px;
  text-align: center;
  padding-top: 8px;
  color: $black;
  background: $primary-color;
  display: inline-block;
  position: fixed;
  content:"";
  right: 5%;
  bottom: 9%;
  opacity: 0;

  &:hover{
    color: $light;
  }
}

.reveal {
    transition: all .3s;
    cursor: pointer;
    opacity: 1;
}