.lh-45{
	line-height: 45px;
}


.skill-bar{
	padding: 80px 10px 0px 10px;
}

.progress{
	height: 5px;
	width: 100%;
	background: rgba(255,255,255,.1);
	overflow: visible;
	position: relative;
	margin: 0 0 35px 0;
	border-radius: 3px;
}

.progress-bar {
    position: absolute;
    width: 0;
    top: 0;
    left: 0;
    height: 100%;
    background: $primary-color;
    border-radius: 3px;
    transition: all 3s ease-in-out;
}

.progress-bar .percent-text {
    font-size: 62px;
    color: $light;
    position: absolute;
    top: -110px;
    left: 0;
}
