
body,html{
  overflow-x:hidden;
  width: 100%;
}
.portfolio{
  background: #131f33;
  color: $light;
}

.navbar-toggle .icon-bar {
  background: $primary-color;
}

input[type="email"],input[type="password"],input[type="text"],input[type="tel"]{
  box-shadow:none;
  height:45px;
  outline: none;
  font-size:14px;
  &:focus {
    box-shadow: none;
    border: 0px;
    border-bottom:1px solid $primary-color;
  }
}


.form-control {
  box-shadow: none;
  border-radius: 0;
  &:focus {
    box-shadow:none;
    border-bottom:1px solid $primary-color;
  }
}

.text-black{
  color: $black;
}

//


// Button Style

.btn{
  display: inline-block;
  font-size: 18px;
  padding: 13px 45px;
  text-transform: capitalize;
  font-weight: 400;
  letter-spacing: 1px;
  border-radius:3px;
  @include transition (all, 0.2s, ease);
  border: 1px solid transparent;
 
  &:hover {
    background: darken( $primary-color, 20% );
    color: $light;
  }
}

.btn-main {
  background: $primary-color;
  color: $black;
  border-color: $primary-color;
 
  &:hover {
    background: transparent;
    color: $light;
    border-color: rgba(255,255,255,.2);
  }
}


.btn-solid-border {
  border-color: rgba(255,255,255,.2);
  background:transparent;
  color:$light;
  &:hover {
    border-color:$primary-color;
    background:$primary-color;
  }
}
.btn-main-border {
  border-color: $primary-color;
  background:transparent;
  color:$primary-color;
  &:hover {
    border-color:$primary-color;
    background:$primary-color;
  }
}


.btn-round {
  border-radius:4px;
}
.btn-round-full {
  border-radius:50px;
}


.btn.active:focus, .btn:active:focus, .btn:focus {
  outline: 0;
}



.btn-small{
  padding: 9px 28px;
}


.bg-shadow {
  background-color: $light;
  box-shadow: 0 16px 24px rgba(0,0,0,.08);
  padding:20px;
}

.bg-gray {
  background:#152136;
}


.btn-light{
  &:hover{
    background:$primary-color;
    border-color: $primary-color;
  }
}

.section {
  padding:100px 0;
}
.section-title {
  margin-bottom: 70px;

  span{
    letter-spacing: 4px;
  }

  .title{
    text-transform: capitalize;
  }
}



.page-title{
  padding: 120px 0 60px;
 
}




// Social Media Icons 

.twitter {
  background: #00aced;
}
.facebook {
  background: #3b5998 ;
  padding:7px 18px;
}
.googleplus{
  background: #dd4b39;
}
.dribbble {
  background: #ea4c89 ;
}
.instagram {
  background:#bc2a8d;
}
 



.overly {
  position: relative;
  &:before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background:#152136;
    opacity: 0.9;
  }
}



.text-lg{
  font-size: 85px;
  line-height: 90px;
}

.py-7{
  padding-top: 15rem !important;
  padding-bottom: 10rem !important;
}

.text-color{
  color: $primary-color;
}

.letter-spacing{
  letter-spacing: 1px;
}

.text-sm{
  font-size: 14px;
}

.text-md{
  font-size: 22px;
}

.text-lg-2{
  font-size: 50px;
  line-height: 56px;
}

.title{
  font-size: 70px;
  line-height: 80px;
  position: relative;
}

.border-top-1{
  border-top: 1px solid #19273e;
}
.border-bottom-1{
  border-top: 1px solid #19273e;
}



a:hover,a:focus{
  outline: 0;
  text-decoration: none;
}

