.portfolio-item{


	&:before{
		position: absolute;
		content: "";
		top: 0;
		left:0px;
		width:100%;
		height: 0;
		background: rgba(225, 163, 76,.95);
		-webkit-transition: all .35s ease;
	   -moz-transition: all .35s ease;
	    -ms-transition: all .35s ease;
	     -o-transition: all .35s ease;
	        transition: all .35s ease;
	        overflow: hidden;
	        z-index: 1;
	}

	.portoflio-item-overlay{
		text-align: center;
		position: absolute;
		-webkit-transform: translateX(0%);
		   -moz-transform: translateX(0%);
		    -ms-transform: translateX(0%);
		     -o-transform: translateX(0%);
		        transform: translateX(0%);
		top: 0%;
		opacity: 0;
		left: 50%;
		margin-left: -20px;
		-webkit-transition: all .35s ease;
		   -moz-transition: all .35s ease;
		    -ms-transition: all .35s ease;
		     -o-transition: all .35s ease;
		        transition: all .35s ease;
		        z-index: 2;

	}

	p{
		color: $light;
	}
	i{
		font-size: 50px;
		color: $secondary-color;
	}

	&:hover:before{
		opacity: 1;
		height: 100%;
	}

	&:hover .portoflio-item-overlay{
		opacity: 1;
		top: 40%;
	}
		
		
}


.owl-carousel .owl-item .portfolio-item img {
    -webkit-transform-style: none;
}


#portfolio .item{
	.text{
		text-align: center;
	}
}


#portfolio .owl-theme .owl-nav [class*="owl-"] {
    color:$secondary-color;
    background: $primary-color;
}

#portfolio .owl-theme .owl-nav {
    margin-top: 30px;
}


/// POrtflio Details

.portfolio-sidebar{

	.card-title{
		text-transform: uppercase;
	}
	ul li{
		padding: 13px 0px;
		text-transform: uppercase;
		letter-spacing: 1px;
		font-weight: 400;
		font-size: 15px;

		span{
			font-size: 13px;
			color:$light;
			opacity: .9;
			font-weight: 600;
		}
	}
}

.bg-primary{
	background: $primary-color!important;
}