@include tablet{
   .navbar-toggler{
    background: $light!important;
  }
  .navigation{
    background: $secondary-color!important;
  }

  .text-lg {
    font-size: 68px;
    line-height: 70px;
  }
  .py-7 {
      padding-top: 6rem !important;
      padding-bottom: 6rem !important;
  }

}


@include mobile{
 .navigation{
    background: $secondary-color!important;
  }
 .navbar-toggler{
    background: $light!important;
  }
  .text-lg {
    font-size: 52px;
    line-height: 52px;
  }
  .title {
    font-size: 39px;
    line-height: 49px;
  }
   .progress-bar .percent-text {
      font-size: 39px;
      top: -70px;
  }
  .skill-bar {
      padding: 32px 10px 0px 10px;
  }

  .testimonial-slider .testimonial-content {
      padding-left: 0px; 
  }
  .testimonial-item i{
    position: relative!important;
    font-size: 60px!important;
  }
  .py-7 {
      padding-top: 6rem !important;
      padding-bottom: 6rem !important;
  }

  .post-share a {
    margin-bottom: 5px;
  }
   .slider-img:before {
    right: -6px;
  }

}


