.blog-item{


  .blog-item-meta{
    margin-top: -42px;
    position: relative;
    padding: 17px 30px;
    margin-left: 20px;
    margin-right: 20px;
    background: #152136;
    text-transform: capitalize;
  }

  a{
    color: $light;

    &:hover{
      color: $primary-color;
    }
  }

  a.learn-more{
    color: $primary-color;
    letter-spacing: 2px;
  }

}


.tags{
  a{
    color: rgba(255,255,255,.8);
    padding: 2px 10px;
    display: inline-block;
    border: 1px solid rgba(255,255,255,.2);
    margin-bottom: 5px;
    text-transform: capitalize;
    font-size: 14px;
  }
}


.blog-navigation{
  a{
   color: $light;
   display: inline-block;
   border: 1px solid rgba(255,255,255,.2);
   padding: 6px 25px;
  }
}

.comment-form .form-control,
.subscribe .form-control{
  border: 1px solid rgba(255,255,255,.2);
}

.post-share a{
   color: $light;
   display: inline-block;
   padding: 6px 25px;
   border-radius: 2px;
}

